import { authAction } from "app/store/ducks/auth/authSlice";
import { useAppDispatch, useAppSelector } from "app/store/Hooks";
import { customAxios, setAccessTokenInAxiosHeaders } from "customAxios";
import InstagramLoading from "InstagramLoading";
import { useEffect, useState } from "react";
import Routes from "Routes";

function App() {
    const [isRefreshTokenChecking, setIsRefreshTokenChecking] = useState(true);
    useEffect(() => {
        const timer = setTimeout(()=>{
            setIsRefreshTokenChecking(false);
        }, 3200);

        return () => clearTimeout(timer)
    }, []);

    return (
        <div className="App">
            {isRefreshTokenChecking ? <InstagramLoading /> : <Routes />}
        </div>
    );
}

export default App;
