import styled from "styled-components";
import { ReactComponent as InstagramLogoLoading } from "assets/Svgs/instagram-logo-loading.svg";
import React, {useState, useEffect} from 'react';
import img1 from "assets/Images/loading/1.png"
import img2 from "assets/Images/loading/2.png"
import img3 from "assets/Images/loading/3.png"
import img4 from "assets/Images/loading/4.png"
import img5 from "assets/Images/loading/5.png"
import img6 from "assets/Images/loading/6.png"
import img7 from "assets/Images/loading/7.png"
import img8 from "assets/Images/loading/8.png"

const StyledInstagramLoading = styled.div`
    position: absolute;
    top: 50%;
    left: 25%;
    margin: -25px 0 0 -25px;
`;

const ImageWrapper = styled.div`
    margin-top: 20px;

    img {
        width: 300px; /* 이미지 크기 조정 */
        height: auto;
    }
`;

const InstagramLoading = () => {
    const imgArr = [
        img1,
        img2,
        img3,
        img4,
        img5,
        img6,
        img7,
        img8,
    ]

    const [currentImageIndex, setCurrentImageIndex] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentImageIndex((prevIndex) => {
                if (prevIndex < imgArr.length - 1) {
                    return prevIndex + 1; // 다음 이미지로 이동
                } else {
                    clearInterval(interval); // 마지막 이미지면 타이머 종료
                    return prevIndex;
                }
            });
        }, 300); // 300ms 간격으로 이미지 변경

        return () => clearInterval(interval); // 컴포넌트 언마운트 시 타이머 정리
    }, [imgArr.length]);

    return (
        <StyledInstagramLoading>
            {/* <InstagramLogoLoading /> */}
            {/* <ImageWrapper> */}
                <img
                    src={imgArr[currentImageIndex]}
                    alt={`Step ${currentImageIndex + 1}`}

                />
            {/* </ImageWrapper> */}
        </StyledInstagramLoading>
    );
};

export default InstagramLoading;
